.search-bar {
  margin-top: 20px;
  font-weight: 300;
  position: relative;
}

.search-bar__input {
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  width: 100%;
  height: 70px;
  padding: 25px 0 25px 62px;
  font-family: Avenir Heavy, sans-serif;
  font-size: 16px;
  font-weight: 800;
}

.search-bar__input::placeholder {
  color: #3d4e61;
  font-family: Avenir Book, sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.search-bar__input:hover {
  box-shadow: 0 4px 4px #00000040;
}

.search-bar__input:focus {
  text-shadow: 0 4px 4px #00000040;
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  outline: none;
  box-shadow: 0 4px 4px #00000040;
}

.search-bar__label {
  position: relative;
}

.search-bar__label:before {
  content: "";
  background: url("Search.ccd3c07b.svg") center / contain no-repeat;
  width: 20px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 26px;
}

.search-bar__input--small {
  height: 42px;
  padding: 11px 0 11px 33px;
  font-size: 14px;
  transition: all .3s;
}

.search-bar__label--small:before {
  width: 13px;
  left: 13px;
}

.search-bar__input--small::placeholder {
